import React, { useState } from 'react';
import {Paper, Button} from '@material-ui/core';

const Quiz = ({quiz, showAnswer}) => {
    const {no, question, answer} = quiz;
    return (
        <Paper id="quiz" variant="outlined">
            <h4>주관식</h4>
            <h1> Q {no}. </h1>
            <h3> {question} </h3>
            <h2 className="answer">
                정답 : &nbsp;&nbsp;
                { showAnswer ? answer[0] : "???"}
            </h2>
        </Paper>
    )
}

export default Quiz;